    color: #000000 !important;
}

.nav__cta {
    background-color: #1B3718;
    padding: 14px 37px;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.5s ease;
    line-height: 16px;
}

.nav__cta:hover {
    background: #FFFFFF;
    color: #1B3718;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.hero {
