    overflow-y: auto;
}

.modal__message_content {
    margin: 0 30px;
    max-width: 1500px;
    height: 40%;
    top: calc(100vh - 70%);
}

.modal__message_title {
    position: relative;
    font-size: 48px;
}

.modal__message_close{
    position: absolute;
    top: -35px;
    right: -20px;
    cursor: pointer;
}

.modal__text {
    margin-top: 30px;
    font-size: 18px;
    font-family: 'Marcellus';
}

.modal__title {
    position: relative;
    margin-bottom: 50px;
    font-family: 'Marcellus SC';
    font-weight: 400;
    font-size: 40px;
    line-height: 49px;
    color: #000000;
}

.modal__inputs {
    display: flex;
    margin-bottom: 29px;
    width: 100%;
    height: 57px;

}

.modal__inputs:first-child {
    margin-bottom: 15px;
}

.modal__inputs:last-child {
    display: flex;
    justify-content: space-between;
}

.modal__inputs>input {
    width: 50%;
    height: 100%;
    padding-left: 85px;
    border: none;
    outline: none;
    background-color: #FFFFFF;
}

.modal__inputs>input::placeholder {
    font-family: 'Marcellus SC';
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: #9ca3af;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.modal__btns {
    display: flex;
    justify-content: flex-end;
    gap: 7px;
    margin-bottom: 40px;
}

.modal__btn {
    font-family: 'Kaisei Decol';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.15em;
    background: none;
    padding: 12px 24px 12px;
    border: 1px solid #000;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all ease .3s;
    cursor: pointer;
}

.modal__btn:hover {
    background-color: #1B3718;
    color: #fff;
}

.modal__btn_green {
    background-color: #1B3718;
    color: #fff;
    transition: all ease .3s;
    cursor: pointer;
}

.modal__btn_green:hover {
    background-color: #F6F3EE;
    color: #000;
}

.modal__cubTitles {
    display: flex;
    margin-bottom: 39px;
}

.modal__cubTitles>h5 {
    width: 49%;
    font-family: 'Marcellus SC';
    font-weight: 400;
    font-size: 32px;
    line-height: 49px;
    color: #000000;    
}

.input__truck {
    background-image: url(./img/Truck.svg);
    background-repeat: no-repeat;
    background-position: 22px center;
}

.input__people {
    background-image: url(./img/People.svg);
    background-repeat: no-repeat;
    background-position: 22px center;
}

.input__gold {
    background-image: url(./img/gold.svg);
    background-repeat: no-repeat;
    background-position: 22px center;
}

.input__kg {
    background-image: url(./img/kg.svg);
    background-repeat: no-repeat;
    background-position: 22px center;
}

.input__cube {
    background-image: url(./img/cube.svg);
    background-repeat: no-repeat;
    background-position: 22px center;
}

.input__mail {
    background-image: url(./img/mail.svg);
    background-repeat: no-repeat;
    background-position: 22px center;
}

.modal__checkbox {
    width: 51%;
    height: 77px;
    gap: 10px;
    background: #F6F3EE;
    display: flex;
    align-items: center;
    gap: 30px;
    font-family: 'Marcellus SC';
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: left;
    color: #000000;
    position: relative;
    background-image: url(./img/check.svg);
    background-repeat: no-repeat;
    background-position-y: center;
    cursor: pointer;
}

.modal__checkbox_active {
    background-image: url(./img/check_active.svg);
}

.modal__checkbox>input {
    visibility: hidden; 
}

.modal__tel {
    width: 49.9% !important;
    background-image: url(./img/tel.svg);
    background-size: contain;
    background-repeat: no-repeat;
    border: 1px solid #000 !important;
}

.modal__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal__error {
    color: red;
}

.modal__submit {
    display: block;
    padding: 15px 70px;
    background: #1b3718;
    border: 1px solid #000;
    outline: none;
    color: #fff;
    font-family: 'Kaisei Decol';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.15em;
    cursor: pointer;
    transition: all ease .3s;
}

.modal__submit:hover {
    color: #000;
    background: none;
}





@media (max-width: 1800px) {
    .large_text_logo {
        left: 0;
    }

    .overlay {
        padding: 10px;
    }

    .footer__nav li a {
        font-size: 52px;
    }
}

@media (max-width: 1350px) {
    .header__logo {
        transform: translate(0, 18px);
    }
}

@media (max-width: 1200px) {
    .nav {
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        column-gap: 30px;
        row-gap: 20px;
    }

    .nav__links {
        order: 1;
    }

    .hero__link {
        order: 3;
    }

    .footer__nav li a {
        font-size: 32px;
    }

    .right__col {
        align-items: flex-end
    }

    .large_text_logo {
        font-size: 100px;
        line-height: 100%;
    }
}

@media (max-width: 880px) {
    .svg {
        top: 50%;
    }

    .footer__nav {
        flex-direction: column;
        gap: 0;
        align-items: flex-end
    }

    .large_text_logo {
        font-size: 100px;
        line-height: 100%;
    }

    .input__group {
        background-color: white;
    }

    .shippinginfo {
        padding-top: 110px;
    }

    .shippinginfo__form {
        top: 52%;
    }

    .search {
        display: flex;
        flex-direction: column;
        background: none;
    }

    .search__btn {
        width: 280px;
        padding: 20px;
    }

    .about__grid {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
        gap: 50px;
    }

    .about__content {
        padding-left: 5%;
    }

    .logo__overlay {
        top: calc(50% - 173px);
    }
}

@media (max-width: 690px) {

    .header__logo {
        width: 100%;
    }

    .large_text_logo {
        display: none;
    }

    .features__grid {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        gap: 65px;
    }

    .row__countries {
        flex-direction: column;
        align-items: flex-start;
    }

    .modal__content {
        padding: 39px 30px 34px;
    }

    .modal__title {
        text-align: center;
    }

    .modal__inputs {
        height: 114px;
        flex-direction: column;
        gap: 4px;
    }

    .modal__inputs>input {
        width: 100%;
        height: 100%;
    }

    .modal__btns {
        display: flex;
        flex-direction: column;
    }

    .modal__cubTitles {
        justify-content: center;
    }

    .modal__cubTitles>h5:first-child {
        text-align: center;
    }

    .modal__cubTitles>h5:last-child {
        display: none;
    }
}